const ResultsBrowsers = (props) => {
    const {arr, category} = props;
    // console.log(arr)
    if(arr.length===0 || category===""){
        return <Response/>
    }
    switch(category){
        case 'digital-art':
            if(arr[0]?(!(arr[0].idpictures)):false){
                // console.log("an error went")
                return <Loading/>
            }
            return <Pictures arr={arr}/>
        case 'traditional-art':
            if(arr[0]?(!(arr[0].iddrawings)):false){
                return <Loading/>
            }
            return <Drawings arr={arr}/>
        case '3dmodels':
            if(arr[0]?(!(arr[0].idphotos)):false){
                return <Loading/>
            }
            return <Photos arr={arr}/>
        case 'animations':
            if(arr[0]?(!(arr[0].idvideos)):false){
                return <Loading/>
            }
            return <Videos arr={arr}/>
        case 'manga':
            if(arr[0]?(!(arr[0].mangaid)):false){
                return <Loading/>
            }
            return <Mangas arr={arr}/>
        case 'apps-and-software':
            if(arr[0]?(!(arr[0].idapps)):false){
                return <Loading/>
            }
            return <Apps arr={arr}/>
        default: 
            return <Response/>
    }
}
